  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

.btn {
  background-color: #5b815d;
  width: 213px;
  height: 57px;
  line-height: 57px;
  display: inline-block;
  text-align: center;
  color: #fffefe;
  font-weight: 600;
  border: none;
  text-decoration: none;
  border-radius: 29px;
  box-shadow: 0px 7px 21px 0px rgba(55, 125, 60, 0.3);
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* General */

a:focus,
a:active,
button:focus,
button:active {
  outline-color: #97c89a;
}

.header-navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

.header .menu {
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.header .menu.isOpened {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
}

.header .navigation__a {
  position: relative;
  text-decoration: none;
  color: black;
  margin: 0 0 6px 20px;
  font-size: 14px;
  font-weight: 400;
}

.header .navigation__a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  border-bottom: 1px solid #97c89a;
  transition: width 0.5s;
}

.header .navigation__a:hover:after {
  width: 100%;
}

.header .navigation__a:nth-child(1) {
  padding: 0;
}

.header .navigation__a:nth-child(5) {
  margin: 0 0 0 53px;
}

.mobile-menu {
  display: none;
  background-color: transparent;
  border: 2px solid black;
  border-right-color: transparent;
  border-left-color: transparent;
  width: 30px;
  height: 20px;
  position: relative;
  transition: 0.3s;
  outline: none;
}

.mobile-menu.closer {
  position: fixed;
  z-index: 5;
  top: 20px;
  right: 20px;
}

.mobile-menu:before {
  content: '';
  display: block;
  height: 2px;
  border-bottom: 2px solid black;
  position: absolute;
  right: 0;
  top: 50%;
  left: 0;
  margin-top: -3px;
  transition: 0.3s;
  outline: none;
}

.mobile-menu:hover,
.mobile-menu:focus,
.mobile-menu:active {
  border-top-color: #97c89a;
  border-bottom-color: #97c89a;
  transform: scaleY(1.3);
}

.mobile-menu:hover:before,
.mobile-menu:active:before,
.mobile-menu:focus:before {
  border-bottom-color: #97c89a;
}

