@media screen and (max-width: 700px) {
  .header .menu {
    display: none;
  }

  .header .navigation__a {
    margin-bottom: 30px;
    color: white;
    text-align: center;
    font-size: 30px;
  }

  .mobile-menu {
    display: block;
  }
}

@media screen and (max-width: 561px) {
  .product {
    margin: 0 auto;
  }
}

@media screen and (max-width: 560px) {
  .product {
    width: calc(75% - 20px);
  }

  .feedback-card {
    width: calc(100% - 20px);
  }

  .advantages-li {
    width: calc(100% - 20px);
  }
}

@media screen and (max-width: 490px) {
  .slogan__btn {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
  }
}

@media screen and (max-width: 468px) {
  .first-screen {
    background-position: -300px;
  }

  .slogan__text {
    font-size: 40px;
    width: calc(100% - 20px);
    margin-bottom: 80px;
  }

  .gradient {
    padding: 0;
  }

  .feedback_colortext {
    margin: 0 auto;
    padding-top: -200px;
    font-size: 40px;
    width: calc(100% - 20px);
  }
}

@media screen and (max-width: 450px) {
  .modal-container_main {
    height: 100%;
  }

  .modal-container .modal-book__p {
    font-size: 10px;
  }

  .modal-container .modal-book__btn {
    padding-top: 5px;
    padding-bottom: 5px;
    line-height: 0;
  }
}